// components/Navbar.js
import Link from 'next/link';
import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';
import axios from '../services/api'; // Ensure axios is imported for logout if needed

export default function Navbar() {
  const [user, setUser] = useState(null);
  const router = useRouter();

  useEffect(() => {
    // On mount, check for user data in localStorage
    const storedUser = JSON.parse(localStorage.getItem('user'));
    if (storedUser) {
      setUser(storedUser);
    }
  }, []);

  // Update localStorage when user state changes (e.g., after login/logout)
  useEffect(() => {
    if (user) {
      localStorage.setItem('user', JSON.stringify(user));
    } else {
      localStorage.removeItem('user');
    }
  }, [user]);


  const handleLogout = () => {
    localStorage.removeItem('token'); // Remove the token
    localStorage.removeItem('user'); // Remove user data
    setUser(null);
    router.push('/');
  };

  return (
    <nav className="bg-white shadow-md p-4 flex justify-between items-center z-10 relative">
      <div className="flex items-center space-x-6">
        <Link href="/" className="text-2xl font-extrabold text-blue-700 hover:text-blue-800 transition duration-300 ease-in-out">
          DevCommunity
        </Link>
        {/* Search Bar - Placeholder */}
        <div className="relative hidden md:block">
          <input
            type="text"
            placeholder="Search posts..."
            className="pl-10 pr-4 py-2 rounded-full border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-400 w-64"
          />
          <svg className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path></svg>
        </div>
      </div>

      <div className="space-x-4 flex items-center">
        {user ? (
          <>
            <Link href="/create" className="bg-purple-600 hover:bg-purple-700 text-white px-4 py-2 rounded-full text-sm font-semibold transition duration-300 ease-in-out">
              Create Post
            </Link>
            <span className="text-gray-700 font-medium">Hi, {user.username}</span>
            <button
              onClick={handleLogout}
              className="bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded-full text-sm font-semibold transition duration-300 ease-in-out"
            >
              Logout
            </button>
          </>
        ) : (
          <>
            <Link href="/login" className="text-blue-600 hover:text-blue-800 font-medium transition duration-200">
              Login
            </Link>
            <Link href="/register" className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-full text-sm font-semibold transition duration-300 ease-in-out">
              Register
            </Link>
          </>
        )}
      </div>
    </nav>
  );
}